  padding-top: 106px
  background-color: #111111
  +deviceWidth()
    padding-top: 50px
  .item-image
    max-width: 480px
  .item-content
    h4, p
      color: #fafafa
  .black-section
    padding-bottom: 48px
    +deviceWidth()
      padding-bottom: 30px
</style>
